:root {
    color-scheme: light;

    /* colors: */
    --main: hsl(240deg 100% 50%);
    --main-2: hsl(195deg 100% 26%);
    --text-color: hsl(0deg 0% 0%);
    --secondary-text-color: hsl(0deg 0% 15%);
    --bg: hsl(180deg 30% 90%);
    --secondary-bg: hsl(180deg 10% 75%);
    --tertiary-bg: hsl(180deg 10% 60%);
    --contrast: hsl(0deg 100% 33%);
    --contrast-2: hsl(5deg 90% 22%);
}
